.header{
grid-column:1;
grid-row:2;

@include responsive(tablet) {
grid-column: 1 / span 2;
grid-row:2;
}
@include responsive(desktop) {
grid-column: 1 / span 3;
grid-row:2;
}
}//end header

.feature-image {
background: no-repeat center center;
background-size: cover;
display: flex;
align-items: center;
color: $color-feature-text;


@include responsive(small-phone) {
min-height: 11.25rem;
}

@include responsive(phone) {
min-height: 12.5rem;
}

@include responsive(tablet) {
min-height: 15.625rem;
}

@include responsive(big-tablet) {
min-height: 21.875rem;
}

@include responsive(desktop) {
min-height: 39.375rem;
}
}//feature-image

.feature-image-text {
max-width: 760px;
margin-top:20px;
margin-right: auto;
margin-left: auto;
//color: $color-feature-text;
@include responsive(small-phone) {
max-width: 80%;
margin-top:0.3125px;
}
@include responsive(phone) {
max-width: 80%;
margin-top:0.625rem;
}
@include responsive(big-tablet) {
max-width: 37.5rem;
margin-top:0.9375rem;
}
@include responsive(desktop) {
max-width: 47.5rem;
margin-top:1.25rem;
}
}//end feature-image-text

.feature-image-text h1 {
@include responsive(small-phone) {
font-size: 1.3rem;
}
@include responsive(phone) {
font-size: 1.3rem;
}
@include responsive(tablet) {
font-size: 1.6rem;
}
@include responsive(big-tablet) {
font-size: 2rem;
}
@include responsive(desktop) {
font-size: 2.3rem;
}
}//end feature-image-text h1

.feature-image-text .intro-description {
font-size: 1.6rem;
//color: $color-feature-text;

@include responsive(small-phone) {
font-size: .75rem;
}
@include responsive(phone) {
font-size: .75rem;
}
@include responsive(tablet) {
font-size: 1rem;
}
@include responsive(big-tablet) {
font-size: 1.4rem;
}
@include responsive(desktop) {
font-size: 1.6rem;
}
}//end feature-imate-text intro-description
