html {
box-sizing: border-box;
font-size: 16px;
line-height: 1.5;
display:block;

@include responsive(small-phone) {
font-size: .9rem;
}
@include responsive(phone) {
font-size:  .9rem;
}
@include responsive(tablet) {
font-size: 1rem;
}
@include responsive(laptop) {
font-size: 1rem;
}
@include responsive(desktop) {
font-size: 1rem;
}
@include responsive(big-desktop) {
font-size: 1rem;
}
} // end html


body {
font-family: $font-family-reset;
color: $color-text;
font-weight: $norm;
background-color: $color-page;
padding-top: $nav;
min-height: 100vh;
overflow-x:hidden;
} // end body
