.wrapper {
display: grid;
grid-template-columns: 1fr;
grid-template-rows: auto;

@include responsive(tablet) {
grid-template-columns: 1fr minmax(auto, 40%);
grid-template-rows: auto;
}

@include responsive(desktop) {
grid-template-columns: minmax(auto,1fr)  2fr minmax(auto,1fr);
grid-template-rows: auto;
grid-column-gap:6%;
}

} // end wrapper


hr {
border-top: 1px solid #BBB;
margin-bottom: 0;
}


pre {
margin: 0 0 10px;
padding: 10px;
border: 1px solid $color-border;
border-radius: 4px;
background-color: $color-code;
}

pre code {
font-size: $small;
font-family: $font-console;
color: $code-blue ;
white-space: pre-wrap; //css3
white-space: moz-pre-wrap; //firefox
white-space: -pre-wrap; //opera 4-6
white-space: -o-pre-wrap; //opera 7
word-wrap: break-word; //internet explorer
@include responsive(small-phone) {
font-size: .8rem;
}
@include responsive(phone) {
font-size: .8rem;
}
}//pre code

code {
font-size: $med;
font-family: $font-console;
color: $code-blue ;
}

.blockquote {
margin: 25px auto;
font-style: italic;
color: $color-quote;
padding: 1.2em 30px 1.2em 75px;
border:none;
position: relative;
background: $color-quote-bg;
}

blockquote::before {
font-family: Arial;
content: "\201C";
color: $color-tealish;
font-size: 4em;
position: absolute;
left: 10px;
top: -10px;
}

.inline-image{
float: right;
margin-left:10px;
}

.inline-image img{
border-radius: 10px;
max-width: 100%;
height: auto;
}

p {
margin-top: 1rem;
margin-bottom:1rem;
font-size:$small;
}

a, .text-primary{
color:$color-pink;
}

.center {
display: block;
margin-left: auto;
margin-right: auto;
}

.p-news {
font-family: $font-news;
font-size: $med;
color: $color-news;
margin-top: 1rem;
margin-bottom:1rem;
}

.ul-news{
display:inline-block;
}

.anchor-top {
position: absolute;
top: 30rem;
}

.scrollbar-measure {
width: 100px;
height: 100px;
overflow: scroll;
position: absolute;
top: -9999px;
}

.btn-outline-secondary {
color: $color-smokey;
background-color:$color-white;
border-color: $color-border;
}

a.btn-outline-secondary:hover {
color: $color-smokey;
background-color: $color-eggshell;
border-color: $color-border-light;
}
