.navigation {
display:grid;
grid-template-columns: 12% minmax(min-content, 10%) minmax(auto, auto) 1fr;
grid-template-rows: 2.2rem auto;
align-items: center;
justify-content:space-between;
grid-column:1;
grid-row:1;
background-color: $color-silhouette-dark;
color:$color-silhouette-light;


@include responsive(tablet) {
grid-column: 1 / span 2;
grid-row:1;
}

@include responsive(desktop) {
grid-column: 1 / span 3;
grid-row:1;
}

.navbar-toggler{
grid-column:1;
grid-row:1;
justify-self:center;
}//end navbar-toggler

.navbar-brand{
grid-column:2;
grid-row:1;
justify-self:center;
font-family:$font-navbar-brand;
font-size: $med-large;
padding: 0;
}//end navbar-brand

.navbar-collapse{
@include responsive(small-phone) {
grid-column:1 /5;
grid-row:2;
}
@include responsive(phone) {
grid-column:1 /5;
grid-row:2;
}
@include responsive(desktop) {
grid-row:1;
grid-column:3;
}
}//end navbar-collapse

.nav-link{
padding-right:.5rem;
padding-left:.5rem;
}//end nav-link

.nav-right-side{
grid-column:4;
grid-row:1;
margin:none;
justify-self:end;

.navbar-icons {
display: grid;
grid-template-columns: repeat(3, minmax(min-content, 1fr));
grid-template-rows:  1fr;
justify-items: space-between;
gap:0.50rem;

& > li > a {
line-height: 20px;
}

@include responsive(laptop-max) {
gap:1.5rem;
}
}//navbar-icons

#search{
grid-column:1;
grid-row:1;
}
#user{
grid-column:2;
grid-row:1;
}
#shop{
grid-column:3;
grid-row:1;
}
}//nav-right-side

}//end navigation

.collapse{
display:inline-flex;
}//end .collapse
